
.mainprofile{
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  padding: 40px 0;
  align-items: center;
  text-align: justify;
}

/* the small profilpicture */
.mainprofile__img {
  display: flex;
  margin-top: 20px;
  min-width: 300px;
  min-height: 300px;
  background-image: url(/img/profil.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 100px;
}

/* picture underline */
.mainprofile__name {
  text-align: center;
}
    /* skill bubbels */
    .bubbels {
      display: flex;
      flex-wrap: wrap;
    }
    
    .bubbels div {
      padding: 4px;
      margin: 4px;
      background-color: rgb(142, 142, 147);
      color: black;
      border-radius: 6px;
      justify-content: space-evenly;
    }
